.headerContainer {
    /* border: 1px solid blue; */
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
}

.headerContainer h1 {
    font-family: 'antonio';
    text-transform: uppercase;
}

.headerList {
    display: flex;
    gap: 2.5rem;
    flex-wrap: wrap;
}

.headerContainer ul li {
    list-style: none;
    font-family: 'league spartan';
    letter-spacing: 1px;
    cursor: pointer;

}

.headerContainer ul a {
    text-decoration: none;
    color: white;

}

.hamburger {
    cursor: pointer;
}

.sideMenu {
    position: fixed;
    top: 5rem;
    right: -100%;
    /* Start hidden */
    width: 100%;
    height: 100vh;
    background: #070724;
    padding: 20px;
    z-index: 100;
    gap: 2rem;
    
}
.border{
    border: 1px solid red;
}
.sideMenu ul {
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    /* align-items: flex-start; */
    /* border: 1px solid yellow; */
    position: relative;
}

.sideMenu ul li{
    /* border: 1px solid green; */
}

.sideMenuList {
    display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        gap: 1rem;
        margin-top: 1rem;
        /* border: 1px solid green; */

}
.chevron{
    /* border: 1px solid white; */
    margin-top: 1rem;
    margin-right: 1rem;
    
}

.sideMenuListItem{
    
    display: flex;
   align-items: center;
    /* border: 1px solid red; */
    justify-content: space-between;
}


  .devider2 {
      width: 100%;
      border-bottom: 0.90px solid #979797;
      position: absolute;
      margin-top: 1rem;
      margin: 1rem auto 1rem auto;
  }

.sideMenu.open {
    left: 0;
}

.dot {
    height: 25px;
    width: 25px;
    border-radius: 50%;
    display: inline-block;



}

@media (max-width: 961px) {
    .headerContainer {
        flex-direction: column;
        gap: 2rem;
    }

    .headerList {
        gap: 1.5rem;
    }
}

@media (max-width: 704px) {
    .headerContainer {
        flex-direction: row;
    }
}